import * as React from "react";
import { EventModel } from "../models/EventModel";
import { EventService } from "../models/EventService";

export interface ListCalendarProps {
  editEvent: (em: EventModel) => void;
}

export class ListCalendarState {
  events: EventModel[];

  constructor() {
    this.events = [];
  }
}

export class ListCalendar extends React.Component<
  ListCalendarProps,
  ListCalendarState
> {
  constructor(props: ListCalendarProps) {
    super(props);
    this.state = new ListCalendarState();
  }

  componentDidMount() {
    EventService.getAllEvents(events => {
      this.setState({ events: events });
    });
  }

  private readonly rxTimeFix = /(\d+[:\.]\d{2})[:\.]00/;

  private formatStart(e: EventModel): string {
    var start = EventModel.StartDateTime(e);

    if (!e.eventAllDay)
      return start.toLocaleTimeString().replace(this.rxTimeFix, "$1");

    return "";
  }

  private formatDuration(e: EventModel): string {
    var s = "";
    if (e.eventAllDay) {
      if (e.eventDurationDays === 1) {
        s = "All Day";
      } else {
        s = `Thru ${EventModel.EndDateTime(e).toLocaleDateString()}`;
      }
    } else {
      if (e.eventDurationHours > 0) s += `${e.eventDurationHours}h`;
      if (e.eventDurationMinutes > 0) s += ` ${e.eventDurationMinutes}m`;
    }

    return s;
  }

  private static months: string[] = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec"
  ];
  private static days: string[] = [
    "Sun",
    "Mon",
    "Tue",
    "Wed",
    "Thu",
    "Fri",
    "Sat"
  ];

  private stackedDate(e: EventModel): any {
    var startDate = EventModel.StartDateTime(e);
    
    if (startDate < new Date() && EventModel.EndDateTime(e) >= new Date())
      startDate = new Date();

    return (
      <div className="col-4 col-md-2 stacked-date-wrapper form-group">
        <div className="stacked-date">
          <span className="month">
            {ListCalendar.months[startDate.getMonth()]}
          </span>
          <span className="date">{startDate.getDate()}</span>
          <span className="day">{ListCalendar.days[startDate.getDay()]}</span>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="calendar-container">
        <h1>List Calendar of {this.state.events.length} Events</h1>
        <div className="calendar-list-container">
          {this.state.events.map(e => (
            <div
              className={`calendar-list-item form-row ${
                e.eventPrivate ? "private" : ""
              }`}
              key={`event${e.eventId}`}
            >
              {this.stackedDate(e)}
              <div className="event-info-wrapper form-group col-8 col-md-9">
                <h2 onClick={event => this.props.editEvent(e)}>
                  {e.eventTitle}
                </h2>
                <p className="desc">{e.eventDescription}</p>
                <div
                  className={`sdlWrapper ${
                    EventModel.EndsInPast(e) ? "past" : ""
                  }`}
                >
                  <span className="start" hidden={e.eventAllDay}>
                    <i className="fas fa-clock" />&nbsp;{this.formatStart(e)}&emsp;
                  </span>
                  <span className="duration">
                    <i className="fas fa-stopwatch" />&nbsp;{this.formatDuration(
                      e
                    )}
                  </span>
                  <span className="location" hidden={!e.eventLocation.trim()}>
                    &emsp;<i className="fas fa-map-marker-alt" />&nbsp;{
                      e.eventLocation
                    }
                  </span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
