import * as React from "react";
import { EventModel } from "../models/EventModel";
import { EventService } from "../models/EventService";

export interface TileCalendarProps {
    editEvent: (em: EventModel) => void;
}

export class TileCalendarState {
    events: EventModel[];

    constructor() {
        this.events = [];
    }
}

export class TileCalendar extends React.Component<TileCalendarProps, TileCalendarState> {
    constructor(props: TileCalendarProps) {
        super(props);
        this.state = new TileCalendarState();
    }

    componentDidMount() {
        EventService.getAllEvents(events => {
            this.setState({ events: events });
        })
    }

    private readonly rxTimeFix = /(\d+[:\.]\d{2})[:\.]00/

    private formatStart(e: EventModel): string {
        var start = EventModel.StartDateTime(e);

        var s: string;
        if (e.eventAllDay) {
            s = start.toLocaleDateString();

            if (e.eventDurationDays > 1) {
                s += ` – ${EventModel.EndDateTime(e).toLocaleDateString()}`;
            }
        } else {
            s = start.toLocaleString().replace(this.rxTimeFix, "$1");
        }

        return s;
    }

    private formatDuration(e: EventModel): string {
        var s = "";
        if (e.eventAllDay) {
            if (e.eventDurationDays === 1) {
                s = "All Day";
            }
        } else {
            if (e.eventDurationHours > 0)
                s += `${e.eventDurationHours}h`;
            if (e.eventDurationMinutes > 0)
                s += ` ${e.eventDurationMinutes}m`;
        }

        return s;
    }

    render() {
        return (
            <div className="calendar-container">
                <h1>Tile Calendar of {this.state.events.length} Events</h1>
                {this.state.events.map(e => <div className={`calendar-tile ${e.eventPrivate ? "private" : ""}`} key={`event${e.eventId}`}>
                    <h2 onClick={(event) => this.props.editEvent(e)} >{e.eventTitle}</h2>
                    <p className="desc">{e.eventDescription}</p>
                    <p className={`start ${EventModel.EndsInPast(e) ? "past" : ""}`}>
                        {this.formatStart(e)}
                        <span className="duration">{this.formatDuration(e)}</span>
                    </p>
                </div>)}
            </div>
        );
    }
}
