import { EventFormState } from "./EventFormState";
import { Question } from "./Question";

export class EventModel {
  public eventId: number;
  public eventTemplate: string;
  public eventTitle: string;
  public eventPrivate: boolean;
  public eventLocation: string;
  public eventStartDate: string;
  public eventStartTime: string;
  public eventDurationHours: number;
  public eventDurationMinutes: number;
  public eventDurationDays: number;
  public eventRepeat: string;
  public eventAllDay: boolean;
  public eventDescription: string;
  public eventWebLink: string;
  public customFields: { [key: string]: any };
  [key: string]: any;

  constructor() {
    this.eventId = 0;
    this.eventTemplate = "";
    this.eventTitle = "";
    this.eventPrivate = false;
    this.eventLocation = "";
    this.eventStartDate = "2018-07-26";
    this.eventStartTime = "14:30";
    this.eventDurationHours = 1;
    this.eventDurationMinutes = 0;
    this.eventDurationDays = 1;
    this.eventRepeat = "None";
    this.eventAllDay = false;
    this.eventDescription = "";
    this.eventWebLink = "";
    this.customFields = {};
  }

  static CreateFromState(state: EventFormState) {
    const em = new EventModel();

    for (var key in em) {
      em[key] = state[key];
    }

    return em;
  }

  static StartDateTime(e: EventModel): Date {
    return new Date(`${e.eventStartDate} ${e.eventAllDay ? "" : e.eventStartTime}`)
  }

  private static readonly millisecondsInMinute = 60 * 1000;
  private static readonly millisecondsInHour = 60 * 60 * 1000;
  private static readonly millisecondsInDay = 24 * 60 * 60 * 1000;

  static EndDateTime(e: EventModel): Date {
    var end = EventModel.StartDateTime(e);

    if (e.eventAllDay) {
      end.setTime(end.getTime() + e.eventDurationDays * this.millisecondsInDay);
    } else {
      end.setTime(end.getTime() + e.eventDurationHours * this.millisecondsInHour + e.eventDurationMinutes * this.millisecondsInMinute);
    }

    return end;
  }

  static EndsInPast(e: EventModel): boolean {
    return EventModel.EndDateTime(e).getTime() < new Date().getTime();
  }

  static IsValid(state: EventFormState) {
    if (!state.eventTemplate) return "No Template";
    if (!state.eventTitle.trim()) return "No Title";
    if (!state.eventLocation.trim()) return "No Location";
    if (state.eventAllDay) {
      if (state.eventDurationDays < 1) {
        return "Too Few Days";
      }
    } else {
      if (state.eventDurationHours + state.eventDurationMinutes < 1)
        return "No Duration";
    }
    for (let i = 0; i < state.questions.length; i++) {
      const q = state.questions[i];
      if (q.required) {
        const val = state.customFields[q.key];

        switch (typeof val) {
          case "string":
            if (!val.trim()) return `No ${q.label}`;
            break;

          case "object":
            if (Array.isArray(val)) {
              if (val.length < 1) return `No ${q.label}`;
              break;
            }
            return `${q.label} is not an Array`;

          default:
            return `No ${q.label}`;
        }
      }
    }
    return null;
  }
}
