import { EventModel } from "./EventModel";
import { Templates } from "../components/EventForm";

type HttpMethod = "GET" | "POST" | "PUT" | "DELETE";

export class EventService {

    private static apiEndPoint = "https://trumbaeventservice.azurewebsites.net/api";

    static loadTemplates(success: (ts: Templates) => void, fail?: (status: number) => void): void {
        this.serviceCall<null, Templates>("GET", "eventtemplates", null, success, fail)
    }

    static getAllEvents(success: (ems: EventModel[]) => void, fail?: (status: number) => void): void {
        this.serviceCall<null, Array<EventModel>>("GET", "events", null, (events) => { success(this.sortEvents(events)) }, fail);
    }

    static createEvent(em: EventModel, success: (id: number) => void, fail?: (status: number) => void): void {
        this.serviceCall<EventModel, number>("POST", "event", em, success, fail);
    }

    static updateEvent(em: EventModel, success: (id: number) => void, fail?: (status: number) => void): void {
        this.serviceCall<EventModel, number>("PUT", `event/${em.eventId}`, em, success, fail);
    }

    static deleteEvent(id: number, success: (deleted: boolean) => void, fail: (status: number) => void): void {
        this.serviceCall<null, boolean>("DELETE", `event/${id}`, null, success, fail);
    }

    private static serviceCall<Tin, Tout>(method: HttpMethod, endpoint: string, sendData: Tin, success: (v: Tout) => void, fail?: (status: number) => void) {
        const xhr = new XMLHttpRequest();
        xhr.open(method, `${this.apiEndPoint}/${endpoint}`, true);
        xhr.setRequestHeader("Content-Type", "application/json");
        xhr.onload = () => {
            if (xhr.status === 200) {
                success(JSON.parse(xhr.responseText) as Tout);
            }
            else if (fail) {
                fail(xhr.status);
            }
        };

        xhr.send(JSON.stringify(sendData));
    }

    static sortEvents(events: EventModel[]): EventModel[] {
        events.sort((a, b) => {
            var aPast = EventModel.EndsInPast(a);
            var bPast = EventModel.EndsInPast(b);

            if (aPast === bPast) {
                return EventModel.StartDateTime(a).getTime() - EventModel.StartDateTime(b).getTime();
            } else {
                return aPast ? -1 : 1;
            }
        });

        return events;
    }
}