import * as React from "react";
import { EventForm } from './EventForm';
import { EventModel } from '../models/EventModel';
import { TileCalendar } from "./TileCalendar";
import { ListCalendar } from "./ListCalendar";

export interface HomeProps {
}

class HomeState {
    currentView: View;
    lastView: View;
    editModel: EventModel;

    constructor() {
        this.currentView = View.tileCalendar;
        this.lastView = View.tileCalendar;
        this.editModel = null;
    }
}

enum View {
    tileCalendar,
    listCalendar,
    addEvent,
    editEvent
}

export class Home extends React.Component<HomeProps, HomeState> {
    constructor(props: HomeProps) {
        super(props);
        this.state = new HomeState();
    }

    private changeView = (newView: View): void => {
        var newLastView = this.state.currentView;
        this.setState({
            currentView: newView,
            lastView: newLastView
        });
    };

    private editEvent = (em: EventModel): void => {
        this.setState({
            editModel: em
        });
        this.changeView(View.editEvent);
    };

    private viewIsCalendar(v: View) {
        return /Calendar$/.test(View[v]);
    }

    private goToCalendar = () => {
        if (this.viewIsCalendar(this.state.lastView)) {
            this.changeView(this.state.lastView);
        } else {
            this.changeView(View.tileCalendar);
        }
    };

    render() {
        return (
            <div className="container">
                <nav>
                    <ul>
                        <li className={this.state.currentView === View.tileCalendar ? "current" : ""} onClick={(event) => this.changeView(View.tileCalendar)}>Tile Calendar</li>
                        <li className={this.state.currentView === View.listCalendar ? "current" : ""} onClick={(event) => this.changeView(View.listCalendar)}>List Calendar</li>
                        <li className={this.state.currentView === View.addEvent ? "current" : ""} onClick={(event) => this.changeView(View.addEvent)}>Add Event</li>
                    </ul>
                </nav>
                {this.state.currentView === View.tileCalendar ? <TileCalendar editEvent={this.editEvent} /> : null}
                {this.state.currentView === View.listCalendar ? <ListCalendar editEvent={this.editEvent} /> : null}
                {this.state.currentView === View.addEvent ? <EventForm goToCalendar={this.goToCalendar} /> : null}
                {this.state.currentView === View.editEvent ? <EventForm model={this.state.editModel} goToCalendar={this.goToCalendar} /> : null}
            </div>
        );
    }
}
