import * as React from "react";
import { EventModel } from "../models/EventModel";
import { EventStartAndDuration } from "./EventStartAndDuration";
import { EventCustomFields } from "./EventCustomFields";
import { Question } from "../models/Question";
import { EventFormState, FormMode } from "../models/EventFormState";
import { EventService } from "../models/EventService";

export interface EventFormProps {
  model?: EventModel;
  goToCalendar: () => void;
}
export interface Templates {
  [index: string]: Question[];
}

export class EventForm extends React.Component<EventFormProps, EventFormState> {
  constructor(props: EventFormProps) {
    super(props);
    const initialState = new EventFormState();

    if (this.props.model) {
      initialState.formMode = FormMode.edit;

      for (let key in this.props.model) {
        initialState[key] = this.props.model[key];
      }
    }

    this.state = initialState;
  }

  //  load the templates asynchronously when the component is "mounted" -- that is, displayed
  componentDidMount() {
    EventService.loadTemplates(templates => {
      const templateNames = Object.keys(templates);

      this.setState({
        templates: templates,
        templateNames: templateNames,
        eventTemplate: templateNames.length > 0 ? templateNames[0] : "",
        questions: templateNames.length > 0 ? templates[templateNames[0]] : []
      });
    });
  }

  stateSetter = (key: string, value: any) => {
    var obj: EventFormState = {} as EventFormState;
    obj[key] = value;
    this.setState(obj);
  };

  stateGetter = (key: string) => {
    return this.state[key];
  };

  customFieldGetter = (key: string) => {
    return this.state.customFields[key];
  };

  customFieldSetter = (key: string, value: any) => {
    var cf = this.state.customFields;
    cf[key] = value;
    return this.setState({ customFields: cf });
  };

  onTemplateChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const newTemplate = event.target.value;
    this.setState({
      eventTemplate: newTemplate,
      questions: this.state.templates[newTemplate]
    });
  };

  onFormSubmit = (event: React.FormEvent<Element>) => {
    event.preventDefault();

    var validity = EventModel.IsValid(this.state);
    if (validity !== null) {
      alert(validity);
      return;
    }

    const em = EventModel.CreateFromState(this.state);

    if (this.state.formMode === FormMode.add) {
      EventService.createEvent(
        em,
        id => this.props.goToCalendar(),
        status => alert(`Failed to create event with status ${status}`)
      );
    } else if (this.state.formMode === FormMode.edit) {
      EventService.updateEvent(
        em,
        id => this.props.goToCalendar(),
        status => alert(`Failed to update event with status ${status}`)
      );
    }
  };

  onDeleteClick = (event: React.MouseEvent) => {
    EventService.deleteEvent(
      this.state.eventId,
      deleted => this.props.goToCalendar(),
      status => alert(`Failed to delete event with status ${status}`)
    );
  };

  render() {
    return (
      <div>
        <div className="formWrapper">
          <form onSubmit={this.onFormSubmit}>
            {this.state.formMode === FormMode.edit && (
              <button
                className="delete"
                type="button"
                onClick={this.onDeleteClick}
              >
                Delete
              </button>
            )}
            <div hidden={this.state.templateNames.length < 2}>
              <div id="templateNav">
                <label>Template</label>
                <select
                  id="eventTemplate"
                  className="form-control"
                  value={this.state.eventTemplate}
                  onChange={this.onTemplateChange}
                >
                  {this.state.templateNames.map(t => (
                    <option value={t} key={t}>
                      {t}
                    </option>
                  ))}
                </select>
              </div>
              <div className="templateDivider" />
            </div>
            <div className="form-row">
              <div className="form-group col-lg-9 col-md-8 col-sm-7 col-12">
                <div className="col-12 templateFormField">
                  <label className="textInputLabel fieldLabel">
                    Event Title
                  </label>
                  <input
                    id="eventTitleInput"
                    value={this.state.eventTitle}
                    onChange={event => {
                      this.stateSetter("eventTitle", event.target.value);
                      this.stateSetter("eventTitleDirty", true);
                    }}
                    type="text"
                    className={`form-control ${
                      this.state.eventTitle.trim() ? "" : "isEmpty"
                      } ${this.state["eventTitleDirty"] ? "isDirty" : ""}`}
                    required
                  />
                </div>
              </div>
              <div className="form-group col-lg-3 col-md-4 col-sm-5 col-12 privateEventWrapper">
                <label className="textInputLabel fieldLabel hiddenLabel d-none d-sm-block">
                  Private Event
                </label>
                <div className="form-check checkBoxWrapper">
                  <input
                    checked={this.state.eventPrivate}
                    onChange={event =>
                      this.setState({ eventPrivate: event.target.checked })
                    }
                    className="form-check-input"
                    type="checkbox"
                    id="eventPrivate"
                  />
                  <label className="form-check-label" htmlFor="eventPrivate">
                    Private Event
                  </label>
                </div>
              </div>
            </div>
            <div className="form-row">
              <div className="form-group col-md-10 col-sm-9 col-8">
                <div className="col-12">
                  <label className="textInputLabel fieldLabel">Location</label>
                  <textarea
                    value={this.state.eventLocation}
                    onChange={event =>
                      this.setState({ eventLocation: event.target.value })
                    }
                    id="location"
                    className="form-control"
                  />
                </div>
              </div>
              <div
                className="form-group col-md-2 col-sm-3 col-4"
                id="addressLinks"
              >
                <label className="textInputLabel fieldLabel hiddenLabel">
                  Address Links
                </label>
                <a href="#">Add Map Link</a>
                <a href="#">Enter Lat/Long</a>
                <a href="#">Recent Links</a>
              </div>
            </div>
            <EventStartAndDuration
              stateSetter={this.stateSetter}
              stateGetter={this.stateGetter}
            />
            <EventCustomFields
              questions={this.state.questions}
              stateSetter={this.customFieldSetter}
              stateGetter={this.customFieldGetter}
            />
            <div className="form-row">
              <div className="form-group col-12">
                <div className="col-12">
                  <label className="textInputLabel fieldLabel">
                    Description
                  </label>
                  <textarea
                    value={this.state.eventDescription}
                    onChange={event =>
                      this.setState({ eventDescription: event.target.value })
                    }
                    id="description"
                    className="form-control"
                  />
                </div>
              </div>
            </div>
            <div className="form-row buttonRow">
              <div className="col-3">
                <button
                  type="button"
                  className="btn btn-danger col-12"
                  onClick={event => this.props.goToCalendar()}
                >
                  Cancel
                </button>
              </div>
              <div className="col-3">
                <button type="button" className="btn btn-secondary col-12">
                  Preview
                </button>
              </div>
              <div className="col-3">
                <button type="button" className="btn btn-info col-12">
                  {this.state.formMode === FormMode.edit ? "Save" : "Create"}{" "}
                  &amp; Copy
                </button>
              </div>
              <div className="col-3">
                <button type="submit" className="btn btn-success col-12" disabled={EventModel.IsValid(this.state) !== null}>
                  {this.state.formMode === FormMode.edit ? "Save" : "Create"}
                </button>
              </div>
            </div>
          </form>
        </div>
        <div>
          <pre>{EventModel.IsValid(this.state)}</pre>
          <pre>
            {JSON.stringify(EventModel.CreateFromState(this.state), null, "\t")}
          </pre>
        </div>
      </div>
    );
  }
}
